import React, { useState } from 'react'
import { useQuery, useMutation, useQueryClient } from 'react-query'
import { useNavigate } from 'react-router-dom'
import { 
  CreditCard, 
  X, 
  Calculator,
  AlertCircle,
  CheckCircle,
  DollarSign,
  Calendar
} from 'lucide-react'
import { toast } from 'react-hot-toast'
import { estadosCuentaService, pagosService, formatters, MESES } from '../services/api'

const RegistrarPago = () => {
  const navigate = useNavigate()
  const queryClient = useQueryClient()

  const [filtros, setFiltros] = useState({
    ao: new Date().getFullYear(),
    mes: 'ENERO'
  })

  const [pagoData, setPagoData] = useState({
    monto_total: 0,
    fecha_pago: new Date().toISOString().split('T')[0],
    // Distribucin del pago
    pago_retroactivo: 0,
    pago_cuota_extra: 0,
    pago_sanciones: 0,
    pago_parqueaderos: 0,
    pago_zonas_comunes: 0,
    pago_otros: 0,
    pago_intereses: 0,
    // Automtico
    pago_capital: 0
  })

  const [distribucionAutomatica, setDistribucionAutomatica] = useState(true)

  // Obtener estado de cuenta del perodo seleccionado
  const { data: estadoCuenta, isLoading: loadingEstado } = useQuery(
    ['estado-cuenta', filtros.ao, filtros.mes],
    () => estadosCuentaService.getByPeriod(filtros.ao, filtros.mes),
    {
      enabled: !!filtros.ao && !!filtros.mes
    }
  )

  // Mutacin para registrar pago
  const pagoMutation = useMutation(
    (data) => pagosService.registrar(data),
    {
      onSuccess: () => {
        toast.success('Pago registrado exitosamente')
        queryClient.invalidateQueries('estados-cuenta')
        queryClient.invalidateQueries('dashboard-stats')
        navigate('/estados-cuenta')
      },
      onError: (error) => {
        toast.error('Error al registrar el pago')
        console.error('Error:', error)
      }
    }
  )

  // Calcular distribucin automtica del pago
  const calcularDistribucionAutomatica = (montoTotal) => {
    if (!estadoCuenta || montoTotal <= 0) {
      return {
        pago_retroactivo: 0,
        pago_cuota_extra: 0,
        pago_sanciones: 0,
        pago_parqueaderos: 0,
        pago_zonas_comunes: 0,
        pago_otros: 0,
        pago_intereses: 0,
        pago_capital: 0
      }
    }

    let montoRestante = parseFloat(montoTotal)
    const distribucion = {
      pago_retroactivo: 0,
      pago_cuota_extra: 0,
      pago_sanciones: 0,
      pago_parqueaderos: 0,
      pago_zonas_comunes: 0,
      pago_otros: 0,
      pago_intereses: 0,
      pago_capital: 0
    }

    // Orden de prioridad para el pago (similar a las frmulas del Excel)
    const conceptos = [
      { campo: 'pago_sanciones', saldo: estadoCuenta.sanciones || 0 },
      { campo: 'pago_cuota_extra', saldo: estadoCuenta.cuota_extraor || 0 },
      { campo: 'pago_parqueaderos', saldo: estadoCuenta.parqueaderos || 0 },
      { campo: 'pago_zonas_comunes', saldo: estadoCuenta.zonas_comunes || 0 },
      { campo: 'pago_otros', saldo: estadoCuenta.otros || 0 },
      { campo: 'pago_intereses', saldo: estadoCuenta.intereses_total || 0 },
      { campo: 'pago_retroactivo', saldo: estadoCuenta.retroactivo || 0 }
    ]

    // Distribuir el pago segn prioridades
    for (const concepto of conceptos) {
      if (montoRestante <= 0) break
      
      const pagoConcepto = Math.min(montoRestante, concepto.saldo)
      distribucion[concepto.campo] = pagoConcepto
      montoRestante -= pagoConcepto
    }

    // Lo que quede va a capital
    if (montoRestante > 0) {
      const saldoCapital = (estadoCuenta.total_capital || 0) - (estadoCuenta.retroactivo || 0)
      distribucion.pago_capital = Math.min(montoRestante, saldoCapital)
    }

    return distribucion
  }

  // Manejar cambio en el monto total
  const handleMontoChange = (nuevoMonto) => {
    setPagoData(prev => ({
      ...prev,
      monto_total: nuevoMonto
    }))

    if (distribucionAutomatica) {
      const nuevaDistribucion = calcularDistribucionAutomatica(nuevoMonto)
      setPagoData(prev => ({
        ...prev,
        ...nuevaDistribucion
      }))
    }
  }

  // Manejar cambio en distribucin manual
  const handleDistribucionChange = (campo, valor) => {
    setPagoData(prev => ({
      ...prev,
      [campo]: parseFloat(valor) || 0
    }))

    // Recalcular monto total si es distribucin manual
    if (!distribucionAutomatica) {
      const nuevoTotal = Object.keys(pagoData)
        .filter(key => key.startsWith('pago_'))
        .reduce((sum, key) => {
          const val = key === campo ? (parseFloat(valor) || 0) : pagoData[key]
          return sum + val
        }, 0)
      
      setPagoData(prev => ({
        ...prev,
        monto_total: nuevoTotal
      }))
    }
  }

  // Validar formulario
  const validarFormulario = () => {
    if (!estadoCuenta) {
      toast.error('Selecciona un perodo vlido')
      return false
    }

    if (pagoData.monto_total <= 0) {
      toast.error('El monto del pago debe ser mayor a 0')
      return false
    }

    if (!pagoData.fecha_pago) {
      toast.error('La fecha de pago es requerida')
      return false
    }

    const totalDistribucion = Object.keys(pagoData)
      .filter(key => key.startsWith('pago_'))
      .reduce((sum, key) => sum + (pagoData[key] || 0), 0)

    if (Math.abs(totalDistribucion - pagoData.monto_total) > 0.01) {
      toast.error('La distribucin del pago no coincide con el monto total')
      return false
    }

    return true
  }

  // Enviar formulario
  const handleSubmit = (e) => {
    e.preventDefault()
    
    if (!validarFormulario()) return

    const dataToSend = {
      ao: filtros.ao,
      mes: filtros.mes,
      ...pagoData,
      monto_total: parseFloat(pagoData.monto_total)
    }

    pagoMutation.mutate(dataToSend)
  }

  const totalDistribuido = Object.keys(pagoData)
    .filter(key => key.startsWith('pago_'))
    .reduce((sum, key) => sum + (pagoData[key] || 0), 0)

  return (
    <div className="max-w-4xl mx-auto space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Registrar Pago</h1>
          <p className="mt-2 text-gray-600">
            Registra un pago para un perodo especfico
          </p>
        </div>
        <button
          onClick={() => navigate('/estados-cuenta')}
          className="btn btn-secondary"
        >
          <X className="h-4 w-4 mr-2" />
          Cancelar
        </button>
      </div>

      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Seleccin de Perodo */}
        <div className="card">
          <div className="card-header">
            <h3 className="card-title">Seleccionar Perodo</h3>
          </div>
          <div className="card-content">
            <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Ao
                </label>
                <input
                  type="number"
                  min="2020"
                  max="2030"
                  value={filtros.ao}
                  onChange={(e) => setFiltros(prev => ({ ...prev, ao: parseInt(e.target.value) }))}
                  className="input"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Mes
                </label>
                <select
                  value={filtros.mes}
                  onChange={(e) => setFiltros(prev => ({ ...prev, mes: e.target.value }))}
                  className="input"
                >
                  {MESES.map(mes => (
                    <option key={mes} value={mes}>{mes}</option>
                  ))}
                </select>
              </div>
            </div>
          </div>
        </div>

        {/* Estado de Cuenta del Perodo */}
        {loadingEstado ? (
          <div className="card">
            <div className="card-content">
              <div className="flex items-center justify-center h-32">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600"></div>
              </div>
            </div>
          </div>
        ) : estadoCuenta ? (
          <div className="card">
            <div className="card-header">
              <h3 className="card-title">Estado de Cuenta - {filtros.mes} {filtros.ao}</h3>
            </div>
            <div className="card-content">
              <div className="grid grid-cols-2 gap-4 sm:grid-cols-3 lg:grid-cols-4">
                <div className="text-center p-3 bg-blue-50 rounded-lg">
                  <p className="text-lg font-semibold text-blue-600">
                    {formatters.currency(estadoCuenta.total_capital)}
                  </p>
                  <p className="text-xs text-gray-600">Total Capital</p>
                </div>
                <div className="text-center p-3 bg-yellow-50 rounded-lg">
                  <p className="text-lg font-semibold text-yellow-600">
                    {formatters.currency(estadoCuenta.intereses_total)}
                  </p>
                  <p className="text-xs text-gray-600">Intereses</p>
                </div>
                <div className="text-center p-3 bg-green-50 rounded-lg">
                  <p className="text-lg font-semibold text-green-600">
                    {formatters.currency(estadoCuenta.total_a_pagar)}
                  </p>
                  <p className="text-xs text-gray-600">Total a Pagar</p>
                </div>
                <div className="text-center p-3 bg-red-50 rounded-lg">
                  <p className="text-lg font-semibold text-red-600">
                    {formatters.currency(estadoCuenta.saldo_pendiente)}
                  </p>
                  <p className="text-xs text-gray-600">Saldo Pendiente</p>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div className="card bg-yellow-50 border-yellow-200">
            <div className="card-content">
              <div className="flex">
                <AlertCircle className="h-5 w-5 text-yellow-400" />
                <div className="ml-3">
                  <h3 className="text-sm font-medium text-yellow-800">
                    No se encontr estado de cuenta
                  </h3>
                  <p className="mt-1 text-sm text-yellow-700">
                    No existe un registro para el perodo seleccionado. 
                    Crea primero el registro mensual.
                  </p>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Informacin del Pago */}
        {estadoCuenta && (
          <>
            <div className="card">
              <div className="card-header">
                <h3 className="card-title">Informacin del Pago</h3>
              </div>
              <div className="card-content">
                <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Monto Total del Pago *
                    </label>
                    <div className="relative">
                      <DollarSign className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                      <input
                        type="number"
                        step="0.01"
                        min="0"
                        value={pagoData.monto_total}
                        onChange={(e) => handleMontoChange(parseFloat(e.target.value) || 0)}
                        className="input pl-10"
                        placeholder="0.00"
                      />
                    </div>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Fecha de Pago *
                    </label>
                    <div className="relative">
                      <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                      <input
                        type="date"
                        value={pagoData.fecha_pago}
                        onChange={(e) => setPagoData(prev => ({ ...prev, fecha_pago: e.target.value }))}
                        className="input pl-10"
                      />
                    </div>
                  </div>
                </div>

                <div className="mt-4">
                  <div className="flex items-center">
                    <input
                      type="checkbox"
                      id="distribucion-automatica"
                      checked={distribucionAutomatica}
                      onChange={(e) => {
                        setDistribucionAutomatica(e.target.checked)
                        if (e.target.checked) {
                          const nuevaDistribucion = calcularDistribucionAutomatica(pagoData.monto_total)
                          setPagoData(prev => ({ ...prev, ...nuevaDistribucion }))
                        }
                      }}
                      className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300 rounded"
                    />
                    <label htmlFor="distribucion-automatica" className="ml-2 text-sm text-gray-700">
                      Distribucin automtica del pago
                    </label>
                  </div>
                  <p className="mt-1 text-xs text-gray-500">
                    La distribucin automtica aplica el pago segn las prioridades establecidas
                  </p>
                </div>
              </div>
            </div>

            {/* Distribucin del Pago */}
            <div className="card">
              <div className="card-header">
                <div className="flex items-center justify-between">
                  <h3 className="card-title">Distribucin del Pago</h3>
                  <div className="text-sm">
                    <span className="text-gray-600">Total distribuido: </span>
                    <span className={`font-semibold ${
                      Math.abs(totalDistribuido - pagoData.monto_total) > 0.01 
                        ? 'text-red-600' 
                        : 'text-green-600'
                    }`}>
                      {formatters.currency(totalDistribuido)}
                    </span>
                  </div>
                </div>
              </div>
              <div className="card-content">
                <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-3">
                  {[
                    { campo: 'pago_capital', label: 'Capital', saldo: (estadoCuenta.total_capital || 0) - (estadoCuenta.retroactivo || 0) },
                    { campo: 'pago_retroactivo', label: 'Retroactivo', saldo: estadoCuenta.retroactivo || 0 },
                    { campo: 'pago_cuota_extra', label: 'Cuota Extra', saldo: estadoCuenta.cuota_extraor || 0 },
                    { campo: 'pago_sanciones', label: 'Sanciones', saldo: estadoCuenta.sanciones || 0 },
                    { campo: 'pago_parqueaderos', label: 'Parqueaderos', saldo: estadoCuenta.parqueaderos || 0 },
                    { campo: 'pago_zonas_comunes', label: 'Zonas Comunes', saldo: estadoCuenta.zonas_comunes || 0 },
                    { campo: 'pago_otros', label: 'Otros', saldo: estadoCuenta.otros || 0 },
                    { campo: 'pago_intereses', label: 'Intereses', saldo: estadoCuenta.intereses_total || 0 }
                  ].map(({ campo, label, saldo }) => (
                    <div key={campo}>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        {label}
                        <span className="text-xs text-gray-500 ml-1">
                          (Saldo: {formatters.currency(saldo)})
                        </span>
                      </label>
                      <input
                        type="number"
                        step="0.01"
                        min="0"
                        max={saldo}
                        value={pagoData[campo]}
                        onChange={(e) => handleDistribucionChange(campo, e.target.value)}
                        disabled={distribucionAutomatica}
                        className={`input ${distribucionAutomatica ? 'bg-gray-50' : ''}`}
                      />
                    </div>
                  ))}
                </div>
              </div>
            </div>

            {/* Resumen */}
            <div className="card bg-gray-50">
              <div className="card-content">
                <div className="grid grid-cols-1 gap-4 sm:grid-cols-3">
                  <div className="text-center">
                    <p className="text-2xl font-bold text-gray-900">
                      {formatters.currency(pagoData.monto_total)}
                    </p>
                    <p className="text-sm text-gray-600">Monto del Pago</p>
                  </div>
                  <div className="text-center">
                    <p className="text-2xl font-bold text-gray-900">
                      {formatters.currency(totalDistribuido)}
                    </p>
                    <p className="text-sm text-gray-600">Total Distribuido</p>
                  </div>
                  <div className="text-center">
                    <p className={`text-2xl font-bold ${
                      Math.abs(totalDistribuido - pagoData.monto_total) > 0.01 
                        ? 'text-red-600' 
                        : 'text-green-600'
                    }`}>
                      {formatters.currency(pagoData.monto_total - totalDistribuido)}
                    </p>
                    <p className="text-sm text-gray-600">Diferencia</p>
                  </div>
                </div>
              </div>
            </div>

            {/* Botones de Accin */}
            <div className="flex justify-end space-x-4">
              <button
                type="button"
                onClick={() => navigate('/estados-cuenta')}
                className="btn btn-secondary"
              >
                Cancelar
              </button>
              <button
                type="submit"
                disabled={pagoMutation.isLoading || !estadoCuenta}
                className="btn btn-primary"
              >
                {pagoMutation.isLoading ? (
                  <>
                    <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                    Registrando...
                  </>
                ) : (
                  <>
                    <CreditCard className="h-4 w-4 mr-2" />
                    Registrar Pago
                  </>
                )}
              </button>
            </div>
          </>
        )}
      </form>
    </div>
  )
}

export default RegistrarPago