import React, { useState } from 'react'
import { useMutation, useQueryClient } from 'react-query'
import { useNavigate } from 'react-router-dom'
import { 
  Save, 
  X, 
  Calculator,
  AlertCircle,
  CheckCircle,
  Info
} from 'lucide-react'
import { toast } from 'react-hot-toast'
import { estadosCuentaService, validators, MESES } from '../services/api'

const NuevoRegistro = () => {
  const navigate = useNavigate()
  const queryClient = useQueryClient()

  const [formData, setFormData] = useState({
    ao: new Date().getFullYear(),
    mes: 'ENERO',
    deuda_anterior_capital: 0,
    cuota_admon: 0,
    retroactivo: 0,
    parqueaderos: 0,
    sanciones: 0,
    zonas_comunes: 0,
    cuota_extraor: 0,
    otros: 0,
    interes_mes: 0.02, // 2% por defecto
    fecha_pago: ''
  })

  const [errors, setErrors] = useState({})
  const [showCalculations, setShowCalculations] = useState(false)

  // Mutacin para crear registro
  const createMutation = useMutation(
    (data) => estadosCuentaService.create(data),
    {
      onSuccess: () => {
        toast.success('Registro creado exitosamente')
        queryClient.invalidateQueries('estados-cuenta')
        navigate('/estados-cuenta')
      },
      onError: (error) => {
        toast.error('Error al crear el registro')
        console.error('Error:', error)
      }
    }
  )

  // Calcular valores automticos (simulando las frmulas del Excel)
  const calcularValores = () => {
    const {
      deuda_anterior_capital,
      cuota_admon,
      retroactivo,
      parqueaderos,
      sanciones,
      zonas_comunes,
      cuota_extraor,
      otros,
      interes_mes
    } = formData

    // Total Capital = Deuda Anterior + Cuota Admin + Retroactivo
    const total_capital = parseFloat(deuda_anterior_capital) + 
                         parseFloat(cuota_admon) + 
                         parseFloat(retroactivo)

    // Intereses sobre deuda anterior
    const intereses_deuda = parseFloat(deuda_anterior_capital) * parseFloat(interes_mes)

    // Intereses total
    const intereses_total = intereses_deuda

    // Total a pagar = Total Capital + Parqueaderos + Sanciones + Zonas Comunes + Cuota Extra + Otros + Intereses
    const total_a_pagar = total_capital + 
                         parseFloat(parqueaderos) + 
                         parseFloat(sanciones) + 
                         parseFloat(zonas_comunes) + 
                         parseFloat(cuota_extraor) + 
                         parseFloat(otros) + 
                         intereses_total

    return {
      total_capital: Math.round(total_capital * 100) / 100,
      intereses_deuda: Math.round(intereses_deuda * 100) / 100,
      intereses_total: Math.round(intereses_total * 100) / 100,
      total_a_pagar: Math.round(total_a_pagar * 100) / 100
    }
  }

  const valoresCalculados = calcularValores()

  // Manejar cambios en el formulario
  const handleInputChange = (field, value) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }))

    // Limpiar error del campo
    if (errors[field]) {
      setErrors(prev => ({
        ...prev,
        [field]: null
      }))
    }
  }

  // Validar formulario
  const validateForm = () => {
    const newErrors = {}

    if (!formData.ao || formData.ao < 2020 || formData.ao > 2030) {
      newErrors.ao = 'El ao debe estar entre 2020 y 2030'
    }

    if (!formData.mes) {
      newErrors.mes = 'El mes es requerido'
    }

    if (formData.cuota_admon < 0) {
      newErrors.cuota_admon = 'La cuota de administracin no puede ser negativa'
    }

    if (formData.interes_mes < 0 || formData.interes_mes > 1) {
      newErrors.interes_mes = 'El inters debe estar entre 0% y 100%'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  // Enviar formulario
  const handleSubmit = (e) => {
    e.preventDefault()
    
    if (!validateForm()) {
      toast.error('Por favor corrige los errores en el formulario')
      return
    }

    const dataToSend = {
      ...formData,
      ...valoresCalculados,
      // Convertir valores numricos
      deuda_anterior_capital: parseFloat(formData.deuda_anterior_capital),
      cuota_admon: parseFloat(formData.cuota_admon),
      retroactivo: parseFloat(formData.retroactivo),
      parqueaderos: parseFloat(formData.parqueaderos),
      sanciones: parseFloat(formData.sanciones),
      zonas_comunes: parseFloat(formData.zonas_comunes),
      cuota_extraor: parseFloat(formData.cuota_extraor),
      otros: parseFloat(formData.otros),
      interes_mes: parseFloat(formData.interes_mes)
    }

    createMutation.mutate(dataToSend)
  }

  return (
    <div className="max-w-4xl mx-auto space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Nuevo Registro Mensual</h1>
          <p className="mt-2 text-gray-600">
            Crea un nuevo registro financiero mensual para el conjunto residencial
          </p>
        </div>
        <button
          onClick={() => navigate('/estados-cuenta')}
          className="btn btn-secondary"
        >
          <X className="h-4 w-4 mr-2" />
          Cancelar
        </button>
      </div>

      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Informacin Bsica */}
        <div className="card">
          <div className="card-header">
            <h3 className="card-title">Informacin del Perodo</h3>
          </div>
          <div className="card-content">
            <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Ao *
                </label>
                <input
                  type="number"
                  min="2020"
                  max="2030"
                  value={formData.ao}
                  onChange={(e) => handleInputChange('ao', parseInt(e.target.value))}
                  className={`input ${errors.ao ? 'border-danger-300' : ''}`}
                />
                {errors.ao && (
                  <p className="mt-1 text-sm text-danger-600">{errors.ao}</p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Mes *
                </label>
                <select
                  value={formData.mes}
                  onChange={(e) => handleInputChange('mes', e.target.value)}
                  className={`input ${errors.mes ? 'border-danger-300' : ''}`}
                >
                  {MESES.map(mes => (
                    <option key={mes} value={mes}>{mes}</option>
                  ))}
                </select>
                {errors.mes && (
                  <p className="mt-1 text-sm text-danger-600">{errors.mes}</p>
                )}
              </div>
            </div>
          </div>
        </div>

        {/* Valores Base */}
        <div className="card">
          <div className="card-header">
            <h3 className="card-title">Valores Base</h3>
            <p className="text-sm text-gray-600">
              Estos valores se utilizan para calcular el total a pagar
            </p>
          </div>
          <div className="card-content">
            <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Deuda Anterior Capital
                </label>
                <input
                  type="number"
                  step="0.01"
                  min="0"
                  value={formData.deuda_anterior_capital}
                  onChange={(e) => handleInputChange('deuda_anterior_capital', e.target.value)}
                  className="input"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Cuota Administracin *
                </label>
                <input
                  type="number"
                  step="0.01"
                  min="0"
                  value={formData.cuota_admon}
                  onChange={(e) => handleInputChange('cuota_admon', e.target.value)}
                  className={`input ${errors.cuota_admon ? 'border-danger-300' : ''}`}
                />
                {errors.cuota_admon && (
                  <p className="mt-1 text-sm text-danger-600">{errors.cuota_admon}</p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Retroactivo
                </label>
                <input
                  type="number"
                  step="0.01"
                  value={formData.retroactivo}
                  onChange={(e) => handleInputChange('retroactivo', e.target.value)}
                  className="input"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Parqueaderos
                </label>
                <input
                  type="number"
                  step="0.01"
                  min="0"
                  value={formData.parqueaderos}
                  onChange={(e) => handleInputChange('parqueaderos', e.target.value)}
                  className="input"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Sanciones
                </label>
                <input
                  type="number"
                  step="0.01"
                  min="0"
                  value={formData.sanciones}
                  onChange={(e) => handleInputChange('sanciones', e.target.value)}
                  className="input"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Zonas Comunes
                </label>
                <input
                  type="number"
                  step="0.01"
                  min="0"
                  value={formData.zonas_comunes}
                  onChange={(e) => handleInputChange('zonas_comunes', e.target.value)}
                  className="input"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Cuota Extraordinaria
                </label>
                <input
                  type="number"
                  step="0.01"
                  min="0"
                  value={formData.cuota_extraor}
                  onChange={(e) => handleInputChange('cuota_extraor', e.target.value)}
                  className="input"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Otros
                </label>
                <input
                  type="number"
                  step="0.01"
                  value={formData.otros}
                  onChange={(e) => handleInputChange('otros', e.target.value)}
                  className="input"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Inters Mensual (%) *
                </label>
                <input
                  type="number"
                  step="0.001"
                  min="0"
                  max="1"
                  value={formData.interes_mes}
                  onChange={(e) => handleInputChange('interes_mes', e.target.value)}
                  className={`input ${errors.interes_mes ? 'border-danger-300' : ''}`}
                />
                {errors.interes_mes && (
                  <p className="mt-1 text-sm text-danger-600">{errors.interes_mes}</p>
                )}
                <p className="mt-1 text-xs text-gray-500">
                  Ejemplo: 0.02 = 2%
                </p>
              </div>
            </div>
          </div>
        </div>

        {/* Clculos Automticos */}
        <div className="card">
          <div className="card-header">
            <div className="flex items-center justify-between">
              <h3 className="card-title">Clculos Automticos</h3>
              <button
                type="button"
                onClick={() => setShowCalculations(!showCalculations)}
                className="btn btn-sm btn-secondary"
              >
                <Calculator className="h-4 w-4 mr-2" />
                {showCalculations ? 'Ocultar' : 'Ver'} Detalles
              </button>
            </div>
          </div>
          <div className="card-content">
            <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-4">
              <div className="text-center p-4 bg-blue-50 rounded-lg">
                <p className="text-2xl font-bold text-blue-600">
                  ${valoresCalculados.total_capital.toLocaleString()}
                </p>
                <p className="text-sm text-gray-600">Total Capital</p>
              </div>
              <div className="text-center p-4 bg-yellow-50 rounded-lg">
                <p className="text-2xl font-bold text-yellow-600">
                  ${valoresCalculados.intereses_total.toLocaleString()}
                </p>
                <p className="text-sm text-gray-600">Intereses Total</p>
              </div>
              <div className="text-center p-4 bg-green-50 rounded-lg">
                <p className="text-2xl font-bold text-green-600">
                  ${valoresCalculados.total_a_pagar.toLocaleString()}
                </p>
                <p className="text-sm text-gray-600">Total a Pagar</p>
              </div>
              <div className="text-center p-4 bg-gray-50 rounded-lg">
                <p className="text-2xl font-bold text-gray-600">
                  ${(parseFloat(formData.interes_mes) * 100).toFixed(2)}%
                </p>
                <p className="text-sm text-gray-600">Tasa Inters</p>
              </div>
            </div>

            {showCalculations && (
              <div className="mt-6 p-4 bg-gray-50 rounded-lg">
                <h4 className="font-medium text-gray-900 mb-3">Detalle de Clculos:</h4>
                <div className="space-y-2 text-sm text-gray-600">
                  <p> Total Capital = Deuda Anterior ({formData.deuda_anterior_capital}) + Cuota Admin ({formData.cuota_admon}) + Retroactivo ({formData.retroactivo})</p>
                  <p> Intereses Deuda = Deuda Anterior ({formData.deuda_anterior_capital})  Tasa ({(parseFloat(formData.interes_mes) * 100).toFixed(2)}%)</p>
                  <p> Total a Pagar = Total Capital + Parqueaderos + Sanciones + Zonas Comunes + Cuota Extra + Otros + Intereses</p>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Informacin Adicional */}
        <div className="card">
          <div className="card-header">
            <h3 className="card-title">Informacin Adicional</h3>
          </div>
          <div className="card-content">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Fecha de Pago (Opcional)
              </label>
              <input
                type="date"
                value={formData.fecha_pago}
                onChange={(e) => handleInputChange('fecha_pago', e.target.value)}
                className="input max-w-xs"
              />
              <p className="mt-1 text-xs text-gray-500">
                Si no se especifica, se usar la fecha actual al registrar un pago
              </p>
            </div>
          </div>
        </div>

        {/* Botones de Accin */}
        <div className="flex justify-end space-x-4">
          <button
            type="button"
            onClick={() => navigate('/estados-cuenta')}
            className="btn btn-secondary"
          >
            Cancelar
          </button>
          <button
            type="submit"
            disabled={createMutation.isLoading}
            className="btn btn-primary"
          >
            {createMutation.isLoading ? (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                Guardando...
              </>
            ) : (
              <>
                <Save className="h-4 w-4 mr-2" />
                Crear Registro
              </>
            )}
          </button>
        </div>
      </form>

      {/* Informacin de ayuda */}
      <div className="card bg-blue-50 border-blue-200">
        <div className="card-content">
          <div className="flex">
            <Info className="h-5 w-5 text-blue-400 mt-0.5" />
            <div className="ml-3">
              <h3 className="text-sm font-medium text-blue-800">Informacin Importante</h3>
              <div className="mt-2 text-sm text-blue-700">
                <ul className="list-disc list-inside space-y-1">
                  <li>Los clculos se realizan automticamente basados en las frmulas del Excel original</li>
                  <li>El Total Capital incluye la deuda anterior, cuota de administracin y retroactivos</li>
                  <li>Los intereses se calculan sobre la deuda anterior capital</li>
                  <li>Todos los valores monetarios deben ingresarse sin separadores de miles</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default NuevoRegistro