import axios from 'axios'

// Configurar la instancia de axios
const api = axios.create({
  baseURL: import.meta.env.VITE_API_URL || 'http://localhost:8000',
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
  },
})

// Interceptor para requests
api.interceptors.request.use(
  (config) => {
    // Agregar token de autenticacin si existe
    const token = localStorage.getItem('token')
    if (token) {
      config.headers.Authorization = `Bearer ${token}`
    }
    return config
  },
  (error) => {
    return Promise.reject(error)
  }
)

// Interceptor para responses
api.interceptors.response.use(
  (response) => {
    return response
  },
  (error) => {
    // Manejar errores globalmente
    if (error.response?.status === 401) {
      // Token expirado o no vlido
      localStorage.removeItem('token')
      window.location.href = '/login'
    }
    return Promise.reject(error)
  }
)

// Servicios de la API

export const estadosCuentaService = {
  // Obtener todos los estados de cuenta
  getAll: async (aoDesde = null, aoHasta = null) => {
    const params = {}
    if (aoDesde) params.ao_desde = aoDesde
    if (aoHasta) params.ao_hasta = aoHasta
    
    const response = await api.get('/api/v1/estados-cuenta', { params })
    return response.data
  },

  // Crear un nuevo estado de cuenta
  create: async (data) => {
    const response = await api.post('/api/v1/estados-cuenta', data)
    return response.data
  },

  // Obtener un estado de cuenta especfico
  getById: async (id) => {
    const response = await api.get(`/api/v1/estados-cuenta/${id}`)
    return response.data
  },

  // Actualizar un estado de cuenta
  update: async (id, data) => {
    const response = await api.put(`/api/v1/estados-cuenta/${id}`, data)
    return response.data
  },

  // Eliminar un estado de cuenta
  delete: async (id) => {
    const response = await api.delete(`/api/v1/estados-cuenta/${id}`)
    return response.data
  }
}

export const pagosService = {
  // Registrar un pago
  registrar: async (data) => {
    const response = await api.post('/api/v1/pagos', data)
    return response.data
  },

  // Obtener historial de pagos
  getHistorial: async (ao = null, mes = null) => {
    const params = {}
    if (ao) params.ao = ao
    if (mes) params.mes = mes
    
    const response = await api.get('/api/v1/pagos', { params })
    return response.data
  }
}

export const healthService = {
  // Verificar el estado de la API
  check: async () => {
    const response = await api.get('/health')
    return response.data
  }
}

// Utilidades para formatear datos
export const formatters = {
  // Formatear moneda colombiana
  currency: (amount) => {
    return new Intl.NumberFormat('es-CO', {
      style: 'currency',
      currency: 'COP',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format(amount || 0)
  },

  // Formatear fecha
  date: (date) => {
    if (!date) return '-'
    return new Intl.DateTimeFormat('es-CO', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    }).format(new Date(date))
  },

  // Formatear fecha corta
  dateShort: (date) => {
    if (!date) return '-'
    return new Intl.DateTimeFormat('es-CO', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
    }).format(new Date(date))
  },

  // Formatear nmero
  number: (num) => {
    return new Intl.NumberFormat('es-CO').format(num || 0)
  }
}

// Constantes tiles
export const MESES = [
  'ENERO', 'FEBRERO', 'MARZO', 'ABRIL', 'MAYO', 'JUNIO',
  'JULIO', 'AGOSTO', 'SEPTIEMBRE', 'OCTUBRE', 'NOVIEMBRE', 'DICIEMBRE'
]

export const AOS_DISPONIBLES = () => {
  const currentYear = new Date().getFullYear()
  const years = []
  for (let i = currentYear - 5; i <= currentYear + 2; i++) {
    years.push(i)
  }
  return years
}

// Validadores
export const validators = {
  // Validar ao
  ao: (ao) => {
    const currentYear = new Date().getFullYear()
    return ao >= 2020 && ao <= currentYear + 5
  },

  // Validar mes
  mes: (mes) => {
    return MESES.includes(mes.toUpperCase())
  },

  // Validar monto
  monto: (monto) => {
    return !isNaN(monto) && monto >= 0
  },

  // Validar email
  email: (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    return emailRegex.test(email)
  }
}

export default api